.filter_bar_container {
    display: flex;
    justify-content: space-around;
}

.filter-bar {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding: 1em;
    background-color: #f5f5f5;
    border-radius: 5px;
    margin-bottom: 1em;
  
    .ant-select,
    .ant-input,
    .ant-radio-group{
      margin-bottom: 1em;
      @media (min-width: 576px) {
        margin-bottom: 0;
      }
    }
  
    .ant-btn-primary {
      @media (min-width: 576px) {
        margin-left: auto;
      }
    }

    .second_row {
      margin-top: 1rem;
      display: flex;
      justify-content: space-between;
      width: 100%;

      .ordering_reset_container {
        display: flex;
        
        .ordering_button {
          margin-right: 10px;
        }
      }
    }
  }